@charset "UTF-8";
.wrap {
  position: absolute;
  left: 0;
  bottom: 40px;
  width: 288px;
  height: 132px;
  margin-left: -144px;
  text-align: left;
  overflow: hidden;
  font-size: 12px;
  font-family: 'Malgun Gothic', dotum, '\B3CB\C6C0', sans-serif;
  line-height: 1.5; }

.wrap * {
  padding: 0;
  margin: 0; }

.wrap .info {
  width: 286px;
  height: 120px;
  border-radius: 5px;
  border-bottom: 2px solid #ccc;
  border-right: 1px solid #ccc;
  overflow: hidden;
  background: #fff; }

.wrap .info:nth-child(1) {
  border: 0;
  box-shadow: 0px 1px 2px #888; }

.info .title {
  padding: 5px 0 0 10px;
  height: 30px;
  background: #eee;
  border-bottom: 1px solid #ddd;
  font-size: 18px;
  font-weight: bold; }

.info .close {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #888;
  width: 17px;
  height: 17px;
  background: url("http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/overlay_close.png"); }

.info .close:hover {
  cursor: pointer; }

.info .body {
  position: relative;
  overflow: hidden; }

.info .desc {
  position: relative;
  margin: 13px 0 0 90px;
  height: 75px; }

.desc .ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.desc .jibun {
  font-size: 11px;
  color: #888;
  margin-top: -2px; }

.info .img {
  position: absolute;
  top: 6px;
  left: 5px;
  width: 73px;
  height: 71px;
  border: 1px solid #ddd;
  color: #888;
  overflow: hidden; }

.info:after {
  content: '';
  position: absolute;
  margin-left: -12px;
  left: 50%;
  bottom: 0;
  width: 22px;
  height: 12px;
  background: url("http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white.png"); }

.info .link {
  color: #5085BB; }

.reservation {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%; }
  @media screen and (max-width: 775px) {
    .reservation {
      width: 90%;
      margin: 0 auto; } }
  .reservation .btn-request {
    font-weight: bold;
    border: 0;
    border-radius: 5px;
    cursor: pointer;
    outline: none;
    width: 100%;
    height: 30px;
    background-color: #FA5858;
    font-size: 15px;
    color: white;
