.backdrop {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10000;
  background-color: rgba(0, 0, 0, 0.3); }
  @media screen and (max-width: 775px) {
    .backdrop {
      left: 0;
      top: 0; } }

.modal {
  background-color: #fff;
  border-radius: 5px;
  position: absolute;
  left: 50%;
  top: 45%;
  width: 500px;
  min-height: 500px;
  margin-left: -250px;
  padding: 30px;
  margin-top: -280px; }
  @media screen and (max-width: 775px) {
    .modal {
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      margin: 0 auto;
      min-height: 0;
      padding: 0; } }
  .modal .close {
    margin: -15px -10px 10px 0;
    text-align: right; }
    @media screen and (max-width: 775px) {
      .modal .close {
        margin: 10px 20px; } }

